<h1 id="after-peter-s-exam" tabindex="-1"><a class="header-anchor" href="#after-peter-s-exam" aria-hidden="true">#</a> After Peter's Exam</h1>
<p>Here are instructions on what students should do after they have received back an exam marked by me.</p>
<h2 id="double-check-my-marking" tabindex="-1"><a class="header-anchor" href="#double-check-my-marking" aria-hidden="true">#</a> Double check my marking</h2>
<p>I'm only a human; I can do mistake. Therefor you should double check that I haven't marked your answers wrong. For each question on the exam I've written a sample answer and marking guidelines. You should find a link to it in a recent announcement on Canvas, but also from the study guide/one of the pages linked to from the study guide.</p>
<p>If you think you have an answer that has received too few points based on the marking guidelines, send me an email at <a href="mailto:peter.larsson-green@ju.se">peter.larsson-green@ju.se</a> and tell me which answer and a motivation for why you think that answer deserves more points (based on my sample answer and my marking guidelines). Use the template in <FigureNumber /> below for the email.</p>
<Figure caption="Template for email for discussing marking. Replace XXX with your own values.">
