</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br></div></div></Figure>
<p>You may do this for at most 3 questions. If I give you more points on any of those 3 questions, you may send me 3 more questions, and so one.</p>
<div class="custom-container danger"><p class="custom-container-title">You can lose points too!</p>
<p>When I check your answers a second time, it can just as well be that I give you fewer points on an answer than what I initially gave you. So if you're smart you ask me to only double check answer you are quite certain you deserve more points on.</p>
</div>
<div class="custom-container danger"><p class="custom-container-title">Contact me ASAP!</p>
<p>You have only a few days to ask me to double check my marking of some of your answers. After a few days I'll report your result to Ladok, so make sure to send me the email before then.</p>
</div>
<div class="custom-container warning"><p class="custom-container-title">Why only 3 answers?</p>
<p>Most students that think they deserve to get more points on their answers don't get that. If you don't get more points on any of the three answers you are most confident you can get more points on, then you won't get more points on any of your other answers as well.</p>
</div>
<h2 id="general-discussion" tabindex="-1"><a class="header-anchor" href="#general-discussion" aria-hidden="true">#</a> General discussion</h2>
<p>Of course you can also send me an email at <a href="mailto:peter.larsson-green@ju.se">peter.larsson-green@ju.se</a> to ask general questions about the exam or if there's something about the exam you just want to discuss.</p>
